import Vue from 'vue'
import Vuex from 'vuex'
import commands from '../commands.js'
// import { start } from 'repl';
import settings from '../lib/settings.js'
import callCommand from '../lib/CallCommand.js'
import controls from '../controls.js'
Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    lang: 'en',
    toolbar:{
      tooltipDelay: 800,
      tooltip: '',
    },
    controls: controls,
    options: {}
  },
  mutations: {
    setTooltip:(state, value) =>{
      state.toolbar.tooltip = value
    },
    loadSettings: (state, value) => {
      state.options = (typeof value === 'string') ? JSON.parse(value) : value;
    },
    setValue: (state, payload) =>{
      console.log(payload.name, payload.value)
      state.controls.values[payload.name] = payload.value
    },

    switchState:(state, payload) => {
      payload.schema[payload.schemaName] = !payload.schema[payload.schemaName]
    },
    switchSiblings:(state, payload) => {
      let siblings = payload.siblings;
      let len = siblings.len;
      let value = payload.schema[payload.schemaName];
      for(var i = 0, l = payload.siblings.length; i < l; i++) {
        let sibling = payload.siblings[i];
        sibling.schema[sibling.schemaName] = !value
      } 
    }
  },
  getters: {
    getSchema: (state) => (path) =>{
      var name;
      var schema = state.schema;
      var pList = path.split('.');
      
      var len = pList.length -1;
      for(var i = 0; i < len; i++){
        name = pList[i];
        if(!schema[name]) schema = {};
        schema = schema[name]
      }
      name = pList[len]
      return {schema: schema, schemaName: name}
    }
  },
  actions: {
    LOAD_SETTINGS: (context) => {
      let path = 'C:/Users/Octopodo/AppData/Roaming/Adobe/CEP/extensions/KT_Settings';
      let content = settings.readSettings(path + '/command-options-model.json')
      context.commit('loadSettings', content.data)
    },

    SAVE_SETTINGS: (context) => {
      let path = 'C:/Users/Octopodo/AppData/Roaming/Adobe/CEP/extensions/KT_Settings';
      settings.saveSettings(context.state.options, path + '/command-options-model.json')
    },

    CALL_COMMAND: (context, command) => {
      // var _command = `KT.cast('ToolbarCommands')['${command}'](${JSON.stringify(context.state.options)})`;
      var _command = `alert('working')`;
      callCommand(_command) 
    },

    MUTATE: (context, payload) => {
      let mutations =  payload.mutations;
      for(var i = 0, l = mutations.length; i < l; i++) {
        context.commit(mutations[i], payload)
      }
    }
  }
})





