<template>
 <div>
    <v-btn
      small
      icon
      :class="`transparent ma-0 pa-0`"
      @click.prevent="click"
      @mouseover.prevent="$_setTooltip()"
      @mouseleave.prevent="$_clearTooltip()"
    >
      <v-icon 
        :color="iconColor"
        :small="!normal"
      >
        {{icon}}
      </v-icon>
      <span
        v-if="$data.$_title != undefined" 
        v-text="$data.$_title"
      ></span>
    </v-btn>
  </div>
</template>

<script>
import ControlBase from './ControlBase.vue'

export default {
  mixins: [
    ControlBase
  ],

  props: {
    normal: {
      type: Boolean,
      default: false
    },
  },
  computed:{
    icon(){
      let icon = this.$data.$_icon;
      return icon
    },
    iconColor() {
      let color = this.$data.$_color || 'white';
      return color
    },
  },
  methods: {
    clic(){
      this.$_click()
    }
    
  },
}

</script>

<style lang="sass" scoped>
.transparent
  background-color: transparent!important
  border-color: transparent!important
</style>