<template>
  <div>
    <v-form class="pa-0 ma-0" ref="form" @submit.prevent>
        <v-text-field 
          dark
          class="pa-0 ma-0 styled-input white--text"
          height="15"
          background-color="grey darken-3"
          color="orange lighten-1"

          counter="4"
          hide-details
          type="number"
          v-model="value"
          @mouseover.prevent="$_setTooltip()"
          @mouseleave.prevent="$_clearTooltip()"
        ></v-text-field>
      </v-form>
  </div>
</template>

<script>

// @mouseover.prevent="help('Baking frame rate. 1 means it bakes each frame')"
// @mouseleave.prevent="help('')"
import ControlBase from '../mixins/ControlBase.vue'
export default {
  mixins: [
    ControlBase
  ],
  data() {
    return {
      inputRules: [
        v => !isNaN(Number(v)) || this.value
      ],
    }
  },
  computed: {
    value: {
      get: function() {
  
        return this.$store.state.controls.values[this.name]
      }, 
      set: function(newValue) {
        
        let value = parseInt(newValue)
        if(isNaN(value)) {
          return
        }
        let payload = {
          schema: this.$data.$_schema,
          schemaName: this.$data.$_schemaName,
          value: value
        }
        this.$store.commit('setValue', {name: this.name, value:newValue} )
      }
    }
  },
  created() {
    // console.log(this.$data)
    // console.log(this.name, this.$data.$_valuePath)
    // console.log(this.$data.$_schema, this.$data.$_schemaName)
  }
}
</script>

<style lang="sass" scoped>
.styled-input
  width: 40px!important
  padding-top: 6px!important
  font-size: 10pt
  font-color: white!important
</style>