


function KT_REQUEST(command){
 
  return new Promise(function(resolve, reject){
    const msg = new VulcanMessage(VulcanMessage.TYPE_PREFIX + 'com.kt.tools.server.kt-request');
    msg.setPayload(command);
    VulcanInterface.dispatchMessage(msg)
    
    resolve()
  })
}

function KT_RESPONSE() {
  
  return new Promise(function(resolve, reject){
    VulcanInterface.addMessageListener(VulcanMessage.TYPE_PREFIX + 'com.kt.tools.server.kt-response', function(response){
      var payload = VulcanInterface.getPayload(response);
    
      VulcanInterface.removeMessageListener(VulcanMessage.TYPE_PREFIX + 'com.kt.tools.server.kt-response')
      resolve(payload)
    })
  })
}


function callCommand(command) {
  
  return new Promise(function(resolve, reject) {
    KT_REQUEST(command).then(function(){
      
      KT_RESPONSE().then(function (response){
        resolve (response)
      })
     
    })
  })

}


export default callCommand