
<script>
import ButtonBase from '../mixins/ButtonBase.vue'
export default {
  mixins: [ButtonBase],
  computed:{
    icon() {
      let icon = (this.value)?  this.$data.$_icon.on: this.$data.$_icon.off;
      return icon
    },
    iconColor() {
      let color = (this.value)? this.$data.$_color.on : this.$data.$_color.off;
      return color
    }
  },
  created: function(){
    
    let color = {};
    let icon = {};
    color.off =(!this.$data.$_color.off)? 'grey--text': this.$data.$_color.off;
    color.on =(!this.$data.$_color.on)? 'orange--text': this.$data.$_color.on; 
    icon.off =(typeof this.$data.$_icon === 'string')? this.$data.$_icon: this.$data.$_icon.off;
    icon.on =(typeof this.$data.$_icon === 'string')? this.$data.$_icon: this.$data.$_icon.on; 
    this.$data.$_color = color;
    this.$data.$_icon = icon
    this.$data.$_mutations.unshift('switchState');
    this.$data.$_doMutate = true
    console.log(this.$data.$_mutations)

  },
}

</script>

