import callCommand from './CallCommand.js'

export default { 

  readSettingsHost: function(path) {
    function KT_READ_SETTINGS (path){
      var config = KT.cast('CommandConfig');
      var settings = config.readJSONSettings(path);
      return settings;
    }

    var command = `${KT_READ_SETTINGS.toString()};\rKT_READ_SETTINGS('${path}')`;
    return callCommand(command);
  },

  
  saveSettingsHost: function(settings, path){
    function KT_SAVE_SETTINGS(content, path) {
      var content = (content instanceof String)? content : JSON.stringify(content, null, 2);
      KT.writeFile(content, path);
    }
    var command = `${KT_SAVE_SETTINGS.toString()};\KT_SAVE_SETTINGS('${JSON.stringify(settings)}', '${path}')`;
    return callCommand(command);
  },

  readSettings: function(path) {
    var content = window.cep.fs.readFile(path);
    return content
  },

  saveSettings: function(settings, path) {
    let content = (settings instanceof String)? settings : JSON.stringify(settings, undefined, 2);
    let command = `
      var options = KT.cast('CommandConfig').Options();
      KT.writeFile(JSON.stringify(options, undefined, 2), '${path}')
      `;
    console.log(command)
    callCommand(command)

  }
}