<template>
  <div></div>
</template>
<script>
export default {
  props: {
    name: {
      type: String,
      required: true
    },
    command: {
      type: Boolean,
      default: false
    },
    mutant: {
      type: Boolean,
      default: false
    },
    methods: {
      type: Array || Function,
      required: false
    }
  },
  computed: {
    value(){
      return this.$store.state.controls.values[this.name]
    },
  }, 
  data() {
    return {
      $_color: this.$store.state.controls.colors[this.name],
      $_command: (this.command)? this.$store.state.controls.commands[this.name] : undefined,
      $_doCommand: this.command,
      $_doMutate: this.mutant,
      $_enabled: undefined,
      $_icon: this.$store.state.controls.icons[this.name],
      $_mutations: this.$store.state.controls.mutations[this.name] || [],
      $_schema: undefined,
      $_schemaName: '',
      $_siblings: this.$store.state.controls.siblings[this.name],
      $_title:  this.$store.state.controls.titles[this.name],
      $_value: this.$store.state.controls.values[this.name],
    }
  },
  

  created: function(){
    if(this.$data.$_valuePath != undefined) {
      
      let schema = this.$store.getters.getSchema(this.$data.$_valuePath)
      
      this.$data.$_schema = schema.schema
      this.$data.$_schemaName = schema.schemaName
    }
    if(this.$parent.$data.$_isGroup) {
      this.$parent.register(this)
    }
  },

  methods: {
    click(){
      this.$_click();
      this.$emit('item-value-changed')
    },
    // $_asignMethods(){
    //   if(typeof this.methods === 'array'){

    //   }
    // },
    $_mutate() {
      let data = {
        value: this.value,
        mutations: this.$data.$_mutations,
        schema: this.$data.$_schema,
        schemaName: this.$data.$_schemaName
      }
      this.$store.dispatch('MUTATE', data)
    },
    $_callCommand() {
      this.$store.dispatch('CALL_COMMAND', this.$data.$_command)
    },
    $_click() {
      if(this.$data.$_doMutate) {
        this.$_mutate()
      }
      if(this.$data.$_doCommand) {
        this.$_callCommand()
      }
    },
    $_setTooltip() {
      let lang = this.$store.state.lang 
      let tooltip = this.$store.state.controls.tooltips[this.name][lang];
      this.$store.commit('setTooltip', tooltip)
    },
    $_clearTooltip(){
      this.$store.commit('setTooltip', '')
    }
  }
}
</script>
