<template>
  <div>
    <v-layout :row="row" :column="column">
      <slot></slot>
    </v-layout>
  </div>
</template>

<script>
export default {
  props: {
    row: {
      type: Boolean,
      default: false
    },
    column: {
      type: Boolean,
      default:false
    },
    // name: {
    //   type: String,
    //   default: 'BASE'
    // }
  },
  computed: {
    value(){
      let items = this.items;
      let value = false;
      for(var i = 0, l = items.length; i < l; i++) {
        value = items[i].value;
        if(value) {
          break
        }
      }
      return value
    },
    valueChanged(){
      return this.$data.$_lastValue != this.value 
    },
    itemValues(){
      let values = [];
      this.items.forEach((item) => {
        values.push(item.value)
      })
      return values
    },
  },
  data() {
    return {
      items: [],
      $_itemValues: [],
      $_isGroup: true,
      $_lastValue: false
    }
  },
  watch: {
    itemValues: function(newVal, oldVal) {
      this.$emit('item-value-changed')
    }
  },
  created(){
    if(this.$parent.$data.$_isGroup) {
      this.$parent.register(this)
    }
  },

  methods: {
    $_mutate(item, value) {
      this.items.forEach((child) => {
        if(child.$data.$_isGroup) {
          return
        }
        let data = {
          value: !value,
          mutations: ['setOption'],
          schema: child.$data.$_schema,
          schemaName: child.$data.$_schemaName
        }
        this.$store.dispatch('MUTATE', data)
      })
    },
    $_listenChilds(item){
      console.log('Setting.listener')
      item.$on('item-value-changed', ()=>{
        console.log('child listened')
        item.$off('item-value-changed');
        this.$_mutate(item.value);
        this.$_listenChilds(item)
      })
    },
    $_stopListeningChilds(){
      this.$off('item-value-changed');
    },
    register(item){
      this.items.push(item);
      item.$on('item-value-changed',() =>{
        this.$data.$_lastValue = this.value
        if(this.$valueChanged) {
          this.$_mutate(item, item.value)
        }
        
       })
    },
  }
}
</script>

