<script>
import BaseGroup from '../mixins/GroupBase.vue'
export default {
  mixins: [
    BaseGroup
  ],
  data() {
    return {
      $_isSwitchGroup: true,
    }
  },


  methods: {
    $_mutate(item, value) {
      console.log('SWITCH MUTATION')
      this.items.forEach((child) => {
        console.log(child.name, ', ' , item.name, child.$data.$_isGroup)
        if(child.name == item.name) {
          return
        }
        if(child.$data.$_isGroup){
          console.log('Mutating child group')
          child.$_mutate(item, value)
          return
        }
        let data = {
          value: !value,
          mutations: ['setOption'],
          schema: child.$data.$_schema,
          schemaName: child.$data.$_schemaName
        }
        this.$store.dispatch('MUTATE', data)
      })
    },
  }
}
</script>

